import { Component, EventEmitter, OnInit, Output } from '@angular/core';
import { Observable } from 'rxjs';
import { take } from 'rxjs/operators';
import { GameSaveLoadService } from 'src/app/services/game-save-load.service';
import { LoginService } from 'src/app/services/login.service';

@Component({
  selector: 'app-game-loads',
  templateUrl: './game-loads.component.html',
  styleUrls: ['./game-loads.component.scss'],
})
export class GameLoadsComponent implements OnInit {
  @Output() openCloseMenu = new EventEmitter();
  @Output() startLevel = new EventEmitter();
  public gameSaves$: Observable<any>;
  constructor(
    private loginService: LoginService,
    private gameSaveLoadService: GameSaveLoadService
  ) {}
  dateFormat = 'MMM d, y, HH:mm:ss';
  ngOnInit(): void {
    this.gameSaves$ = this.gameSaveLoadService.getGameSaves(this.loginService.currentUserId);
  }

  public loadGame(saveId: string) {
    this.gameSaveLoadService
      .getGameData(saveId, this.loginService.currentUserId)
      .pipe(take(1))
      .subscribe((gameSave) => {
        this.openCloseMenu.emit(-1);
        this.startLevel.emit({ preloadData: gameSave });
      });
  }
}
