import { Component, EventEmitter, Input, OnDestroy, OnInit, Output } from '@angular/core';
import { Observable, of, Subject } from 'rxjs';
import { filter, switchMap, takeUntil } from 'rxjs/operators';
import { GameSaveLoadService } from 'src/app/services/game-save-load.service';
import { LoginService } from 'src/app/services/login.service';
import { getLevelStats } from '../scripts/draw';

@Component({
  selector: 'app-finish-window',
  templateUrl: './finish-window.component.html',
  styleUrls: ['./finish-window.component.scss'],
})
export class FinishWindowComponent implements OnInit, OnDestroy {
  @Input() levelFinishedEmitter: Observable<void>;
  @Input() finishTime: string;
  @Output() startNewLevel = new EventEmitter();
  public isModalDisplayed = false;
  public resultsTable$: Observable<any>;
  public currentResultId: string;
  public isLoaderDisplayed: boolean = false;
  private ngUnsubscribe$ = new Subject();

  constructor(private gameSavesService: GameSaveLoadService, private loginService: LoginService) {}

  ngOnInit() {
    this.levelFinishedEmitter
      .pipe(
        takeUntil(this.ngUnsubscribe$),
        switchMap(() => {
          this.isModalDisplayed = true;
          this.isLoaderDisplayed = true;
          return this.loginService.isAuthorized();
        }),
        switchMap((isAuthorized: boolean) => {
          return isAuthorized
            ? this.gameSavesService.saveLevelFinishStats(
                getLevelStats(),
                this.loginService.currentUserId
              )
            : of({ resultId: null });
        })
      )
      .subscribe(({ resultId }) => {
        this.isLoaderDisplayed = false;
        this.currentResultId = resultId;
        this.resultsTable$ = this.gameSavesService.getLevelTableStats(getLevelStats().size);
      });
  }

  ngOnDestroy() {
    this.ngUnsubscribe$.next();
    this.ngUnsubscribe$.complete();
  }

  public restartLevel() {
    this.startNewLevel.emit(true);
    this.isModalDisplayed = false;
  }

  public getTop5Results(results): any[] {
    return results.slice(0, 5);
  }

  public getExactResultInfo(id: string, results: any[]): any {
    let resultIndex = 0;
    return {
      ...results.find((element, index) => element._id === id && (resultIndex = index + 1)),
      index: resultIndex,
    };
  }
}
