import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { from, Observable } from 'rxjs';
import { URL } from '../../configuration/apiUrl';
@Injectable({
  providedIn: 'root',
})
export class LoginService {
  constructor(private router: Router, private http: HttpClient) {}
  public isLoggedIn(): boolean {
    return !!localStorage.getItem('userId');
  }

  public logInAsGuest(): void {
    this.setUserIdToLocalStorage('guest');
    this.router.navigateByUrl('/game');
  }

  public setUserIdToLocalStorage(userId: string): void {
    localStorage.setItem('userId', userId);
  }

  public logIn({ login, password }: { login: string; password: string }): Observable<any> {
    return this.http.post<any>(`${URL}/login`, { login, password });
  }

  public register({ login, password }: { login: string; password: string }): Observable<any> {
    return this.http.post<any>(`${URL}/register`, { login, password });
  }

  public isAuthorized(): Observable<any> {
    return this.http.get<any>(`${URL}/user-exists/${localStorage.getItem('userId')}`);
  }

  public logOut(): void {
    localStorage.removeItem('userId');
    this.router.navigateByUrl('/login');
  }

  public get currentUserId(): string {
    return localStorage.getItem('userId');
  }
}
