import { BehaviorSubject } from 'rxjs';

let canvas: HTMLCanvasElement;
let canvasPlayWindow: HTMLCanvasElement;
let body: HTMLBodyElement;
let ctx: CanvasRenderingContext2D;
let ctxPlay: CanvasRenderingContext2D;

let currentLevel: Level;
let currentPlayer: Player;
const defaultMoveVelocity = 31;
const defaultRotateVelocity = 3.2;
const textureDim = 64;
const defaultLevelSize = 40;
const rayCount = 221;
const FOV = 70;
const halfFOV = FOV / 2;
const mainColor: string = '#000';
const secondaryColor: string = '#666';
const thirdColor: string = '#5d7';
const playerColor: string = '#1d7';
const canvasWidth: number = 1178;
const canvasHeight: number = 512;
const playerEyesVerticalPosition = [-10, 15];
const playerEyesVerticalChange = [-1.4, 1];
const fpsLimit = 61;
const ignoreMask = false;
export const isLevelFinished = new BehaviorSubject(false);
let defaultTextureId = 1;
let textures = new Image();
textures.src = './assets/images/walls.png';

// var itemTypes = [{ img: './assets/lamp.png', block: false }];

var mapItemsAndEnemies = [];

class Enemy {
  player: Player;
  level: Level;
  x: number;
  y: number;
  isVisible: boolean;
  constructor(player: Player, level: Level) {
    this.player = player;
    this.level = level;
    this.isVisible = false;
    this.x = level.tileSize * 2.5;
    this.y = level.tileSize * 2.5;
  }
}

function normilizeAngle(angle: number) {
  angle = angle % (2 * Math.PI);

  if (angle < 0) {
    angle += 2 * Math.PI;
  }

  return angle;
}

export function getIsLevelFinished() {
  return isLevelFinished.value;
}

export function getLevelStats() {
  return { size: currentLevel.tilesCount, time: levelTimer.time / 1000 };
}

function calculateDistance(x1: number, y1: number, x2: number, y2: number) {
  return Math.sqrt((x2 - x1) ** 2 + (y2 - y1) ** 2);
}

function convertToRadians(angle: number) {
  return angle * (Math.PI / 180);
}

export function createGameSave() {
  return {
    playerPreload: currentPlayer.getPlayerData(),
    levelPreload: currentLevel.getLevelData(),
    levelTime: levelTimer.time,
    timestamp: new Date(),
  };
}
