import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { shareReplay, take } from 'rxjs/operators';
import { GameSaveLoadService } from 'src/app/services/game-save-load.service';
import { LoginService } from 'src/app/services/login.service';
import { createGameSave, startLevel } from '../scripts/draw';

@Component({
  selector: 'app-menu',
  templateUrl: './menu.component.html',
  styleUrls: ['./menu.component.scss'],
})
export class MenuComponent implements OnInit {
  @Input() isAuthorizedUser;
  @Output() openCloseMenu = new EventEmitter();
  @Output() startLevel = new EventEmitter();
  constructor(
    private loginService: LoginService,
    private gameSaveLoadService: GameSaveLoadService
  ) {}

  ngOnInit(): void {}

  public logOut(): void {
    this.loginService.logOut();
  }

  public loadGame() {
    this.openCloseMenu.emit(1);
  }

  public startLevelNewLevel() {
    this.openCloseMenu.emit(-1);
    this.startLevel.emit(true);
  }

  public saveGame() {
    this.gameSaveLoadService
      .saveGameData(createGameSave(), this.loginService.currentUserId)
      .pipe(take(1))
      .subscribe(() => {
        this.openCloseMenu.emit(-1);
      });
  }
}
