import { Component, OnDestroy, OnInit } from '@angular/core';
import { Subject } from 'rxjs';
import { shareReplay } from 'rxjs/operators';
import { LoginService } from '../services/login.service';
import {
  startLevel,
  startGameInterval,
  clearGameInterval,
  levelTimer,
  getIsLevelFinished,
  playerStopMoving,
  playerStopRotating,
  playerMoveBack,
  playerMoveForward,
  playerRotateLeft,
  playerRotateRight,
  animationData,
} from './scripts/draw';

@Component({
  selector: 'app-game-level',
  templateUrl: './game-level.component.html',
  styleUrls: ['./game-level.component.scss'],
})
export class GameLevelComponent implements OnInit, OnDestroy {
  constructor(private loginService: LoginService) {}
  public levelFinishedEmitter = new Subject();
  public isLevelFinished: boolean;
  public isAuthorizedUser$;
  public currentMenuId = -1;
  private interval;
  private ESCBtnPressHandler = (event: KeyboardEvent) => {
    if (event.keyCode === 27 && !this.isLevelFinished) {
      this.openCloseMenu();
    }
  };

  ngOnInit() {
    this.startLevel(true);
    this.initRestartKeyListener();
    document.addEventListener('keydown', this.ESCBtnPressHandler);
    this.isAuthorizedUser$ = this.loginService.isAuthorized().pipe(shareReplay(1));
  }

  ngOnDestroy() {
    document.removeEventListener('keydown', this.ESCBtnPressHandler);
  }

  public get levelTime() {
    return (levelTimer.time / 1000).toFixed(1);
  }

  public get animationData() {
    return animationData;
  }

  public get fps() {
    return animationData.fps ^ 0;
  }

  public openCloseMenu(menuId: number = null): void {
    if (this.currentMenuId == 2 && menuId != -1) {
      this.generateLevel();
      this.currentMenuId = -1;
    } else if (this.currentMenuId == -1) {
      ++this.currentMenuId;
    } else if (!menuId) {
      --this.currentMenuId;
    } else {
      this.currentMenuId = menuId;
    }
    if (this.currentMenuId == -1) {
      startGameInterval();
    } else {
      clearGameInterval();
    }
  }

  public initRestartKeyListener(): void {
    document.addEventListener('keydown', (event: KeyboardEvent) => {
      if (event.keyCode === 82 && !this.isLevelFinished) {
        this.startLevel(true);
      }
    });
  }

  public startLevel(
    askLevelSize = false,
    { preloadData, size } = { preloadData: null, size: null }
  ) {
    if (askLevelSize) {
      this.currentMenuId = 2;
      clearGameInterval();
    } else {
      this.generateLevel({ preloadData, size, textureId: null });
    }
  }

  public generateLevel(
    { preloadData, size, textureId } = { preloadData: null, size: null, textureId: null }
  ) {
    this.isLevelFinished = false;
    clearInterval(this.interval);
    startLevel(preloadData, size, textureId);
    this.interval = setInterval(() => {
      if (getIsLevelFinished()) {
        clearInterval(this.interval);
        this.finishLevel();
        this.levelFinishedEmitter.next();
      }
    }, 100);
  }

  public finishLevel(): void {
    clearGameInterval();
    this.isLevelFinished = true;
  }

  public moveForward() {
    playerMoveForward();
  }

  public moveBackward() {
    playerMoveBack();
  }

  public rotateLeft() {
    playerRotateLeft();
  }

  public rotateRight() {
    playerRotateRight();
  }

  public stopRotating() {
    playerStopRotating();
  }

  public stopMoving() {
    playerStopMoving();
  }
}
