import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { Observable } from 'rxjs';
import { URL } from '../../configuration/apiUrl';
@Injectable({
  providedIn: 'root',
})
export class GameSaveLoadService {
  constructor(private router: Router, private http: HttpClient) {}

  public saveGameData(payload: any, userId: string): Observable<any> {
    return this.http.post<any>(`${URL}/save-game/${userId}`, payload);
  }

  public getGameData(saveId: string, userId: string): Observable<any> {
    return this.http.get<any>(`${URL}/load-game/${saveId}/${userId}`);
  }

  public getGameSaves(userId: string): Observable<any> {
    return this.http.get<any>(`${URL}/game-saves/${userId}`);
  }

  public saveLevelFinishStats(
    { time, size }: { time: number; size: number },
    userId: string
  ): Observable<any> {
    return this.http.post<any>(`${URL}/level-stats/`, { time, size, userId });
  }

  public getLevelTableStats(size: number): Observable<any> {
    return this.http.get<any>(`${URL}/level-stats/${size}`);
  }
}
